@import '../../globalVariable';

$toastHeight: 44px;

.twoFactorHelper {
    margin: 20px 30px 20px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    .textWrapper {
        .title {
            font-size:20px;
            color:rgba(0,0,0,1);
            line-height:22px;
        }
        .content {
            // padding-inline-start: 15px;
            margin-top: 20px;
            font-size: 14px;
            font-weight:500;
            color:rgba(0,0,0,1);
            line-height:17px;
            font-family:PingFangSC-Medium;
            .block {
                margin-bottom: 20px;
                .item {
                    display: flex;
                    .order {
                        margin-right: 5px;
                    }
                }
            }
        }
    } 
    @mixin separator {
       color: #E4E4E4;
       margin-bottom: 20px;
    }

    .titleSeparator {
        @include separator;
        margin-top: 17px;
    }

    .messageSeparator {
        @include separator;
        margin-top: 30px;
    }

    .backupSeparator {
        @include separator;
        margin-top: 40px;
        margin-bottom: 38px;
    }

    .messageBtn {
        margin-top: 20px;
        font-family: PingFangSC-Light;
    }
    .useBackupBtn {
        @include basicButton;
        margin-top: 30px;
    }
    .completeBtn {
        background: none;
        border: none;
        font-size: 13px;
        height:18px;
        min-height: 18px;
        margin-top: 20px;
        color:rgba(77,77,77,1);
        line-height:18px;
        -webkit-tap-highlight-color: transparent;
    }
}

